"use client";

import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Calendar } from "@/components/ui/calendar";
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover";
import { Header } from "@/components/header";
import { cn } from "@/lib/utils";
import { format } from "date-fns";
import { Calendar as CalendarIcon, MapPin, User2 } from "lucide-react";
import { useState } from "react";
import { useRouter } from "next/navigation";

export default function HomePage() {
  const router = useRouter();
  const [date, setDate] = useState<Date>();

  return (
    <div className="min-h-screen bg-background">
      <Header showMenu />
      <div className="bg-primary text-white px-6 pb-6">
        <div className="space-y-2">
          <h2 className="text-2xl font-bold">Hello, User!</h2>
          <p className="opacity-90">Where are you going today?</p>
        </div>
      </div>

      {/* Search Form */}
      <Card className="mx-6 -mt-6 p-6 shadow-lg">
        <form className="space-y-4">
          <div className="space-y-4">
            <div className="relative">
              <MapPin className="absolute left-3 top-3 h-6 w-6 text-muted-foreground" />
              <Input
                placeholder="From"
                className="pl-12 h-12"
              />
            </div>
            <div className="relative">
              <MapPin className="absolute left-3 top-3 h-6 w-6 text-muted-foreground" />
              <Input
                placeholder="To"
                className="pl-12 h-12"
              />
            </div>
            <div className="relative">
              <Popover>
                <PopoverTrigger asChild>
                  <Button
                    variant="outline"
                    className={cn(
                      "w-full h-12 pl-12 justify-start text-left font-normal",
                      !date && "text-muted-foreground"
                    )}
                  >
                    <CalendarIcon className="absolute left-3 top-3 h-6 w-6" />
                    {date ? format(date, "PPP") : "Select date"}
                  </Button>
                </PopoverTrigger>
                <PopoverContent className="w-auto p-0">
                  <Calendar
                    mode="single"
                    selected={date}
                    onSelect={setDate}
                    initialFocus
                  />
                </PopoverContent>
              </Popover>
            </div>
            <div className="relative">
              <User2 className="absolute left-3 top-3 h-6 w-6 text-muted-foreground" />
              <Input
                placeholder="Number of passengers"
                type="number"
                className="pl-12 h-12"
              />
            </div>
          </div>

          <Button 
            type="submit"
            className="w-full h-12 text-lg font-semibold bg-primary"
            onClick={(e) => {
              e.preventDefault();
              router.push('/search-results');
            }}
          >
            Search Buses
          </Button>
        </form>
      </Card>

      {/* Quick Links */}
      <div className="p-6">
        <h3 className="text-lg font-semibold mb-4">Quick Links</h3>
        <div className="grid grid-cols-3 gap-4">
          {['My Bookings', 'Support', 'Settings'].map((item) => (
            <Card 
              key={item}
              className="p-4 text-center cursor-pointer hover:bg-accent"
              onClick={() => router.push(`/${item.toLowerCase().replace(' ', '-')}`)}
            >
              <p className="text-sm font-medium">{item}</p>
            </Card>
          ))}
        </div>
      </div>
    </div>
  );
}
